import React from "react";
import { Outlet } from "react-router-dom";

import appRoutes from "shared/constants/appRoutes";
import Tools from "shared/components/tools";
import { getLastPartInTitleCase } from "shared/helpers/string";
import forexIcon from "shared/images/forex.svg";
import chitIcon from "shared/images/chit.svg";

function Money() {
  return (
    <>
      <Tools listMap={toolsListMap} />
      {/* Outlet for child routes */}
      <Outlet />
    </>
  );
}

const toolsListMap = new Map([
  [
    appRoutes.toolsMoneyForex.path,
    {
      name: getLastPartInTitleCase(appRoutes.toolsMoneyForex.path, "/"),
      icon: forexIcon,
      description: appRoutes.toolsMoneyForex.title
    }
  ],
  [
    appRoutes.toolsMoneyChit.path,
    {
      name: getLastPartInTitleCase(appRoutes.toolsMoneyChit.path, "/"),
      icon: chitIcon,
      description: appRoutes.toolsMoneyChit.description
    }
  ]
]);

export default Money;
